/* eslint-disable @typescript-eslint/naming-convention */
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { EventsService } from 'src/app/services/events.service';
import { LocalService } from 'src/app/services/local.service';
import { RestService } from 'src/app/services/rest.service';
import { UtilityService } from 'src/app/services/utility.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-cleanerprofile',
  templateUrl: './cleanerprofile.page.html',
  styleUrls: ['./cleanerprofile.page.scss'],
})
export class CleanerprofilePage implements OnInit {
  id: any;
  cleaner: any;
  inFav = false;
  loggedUser: any;
  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private location: Location
  ) {
    this.loggedUser = this.localService.getUser();
    this.activatedRoute.queryParams.subscribe((params) => {
      if (params.id) {
        this.id = params.id;
        this.utilityService.loadingOpen('Loading...');
        this.restService.getData('cleaner/get/' + this.id).subscribe(
          (data) => {
            this.cleaner = data;
            this.utilityService.loadingClose();
          },
          (err) => {}
        );
        // get favourites
        this.restService.getData('user/get/' + this.loggedUser.id).subscribe(
          (data) => {
            const favourites = data.favourites;
            if (favourites) {
              // check for current cleaner
              favourites.forEach((item) => {
                if (item.cleaner_id === parseInt(this.id)) {
                  this.inFav = true;
                }
              });
            }
          },
          (err) => {}
        );
      } else {
        this.location.back();
      }
    });
  }

  ngOnInit() {}
  openGallery() {
    this.router.navigate(['/cleanergallery'], {
      queryParams: {
        cleaner_id: this.id
      }
    });
  }

  bookCleaner() {
    this.router.navigate(['/book'], {
      queryParams: {
        cleaner_id: this.id
      }
    });
  }

  unfav() {
    // unfavourite
    this.restService
      .postData('favourite/delete', {
        cleaner_id: this.id,
        user_id: this.loggedUser.id,
      })
      .subscribe(
        (data) => {
          this.inFav = false;
        },
        (err) => {}
      );
  }

  fav() {
    // favourite
    this.restService
      .postData('favourite/create', {
        cleaner_id: this.id,
        user_id: this.loggedUser.id,
      })
      .subscribe(
        (data) => {
          this.inFav = true;
        },
        (err) => {}
      );
  }

  openNotifications() {
    this.router.navigate(['/cleanernotifications']);
  }
}
